import {authApi as authAPI} from "../api/api";

const SET_USER_DATA = 'SET_USER_DATA';
const UPDATE_USER_DATA = 'UPDATE_USER_DATA';
const DELETE_USER_DATA = 'DELETE_USER_DATA';
const SET_AUTH = 'SET_AUTH';
const SET_LOGIN_WRONG = 'SET_LOGIN_WRONG';
const SET_INITIALIZED = 'SET_INITIALIZED';
const SET_PASSWORD_CONDITIONS = 'SET_PASSWORD_CONDITIONS';
const SET_IS_CURRENT_PASSWORD_WRONG = 'SET_IS_CURRENT_PASSWORD_WRONG';
const SET_IS_PASSWORD_RIGHT = 'SET_IS_PASSWORD_RIGHT';
const SET_IS_PASSWORD_SIMPLE = 'SET_IS_PASSWORD_SIMPLE';
const SET_IS_EMAIL_EXISTS = 'SET_IS_EMAIL_EXISTS';
const SET_IS_EMAIL_TAKEN = 'SET_IS_EMAIL_TAKEN';
const IS_FETCH = 'IS_FETCH';
const SET_USER_STATUS = 'SET_USER_STATUS';


let initialState = {
    userData: {}, // 

    isLogin: false, //  

    isUserServiceAdmin: false, //    
    isUserMuseumSuperAdmin: false, //     
    isUserMuseumAdmin: false, //    
    isUserCashier: false, //   

    isInitialized: false, // 
    isFetch: true,
    isLoginWrong: false, //   
    isSetPasswordRight: false, //   
    isCurrentPasswordWrong: false, //   
    isEmailExists: true, //     email
    isEmailTaken: false, //   email
    isPasswordSimple: false, //  
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA: //  
            return {
                ...state,
                userData: action.userData,
            }
        case UPDATE_USER_DATA: //  
            return {
                ...state,
                userData: {
                    ...state.userData,
                    first_name: action.first_name,
                    last_name: action.last_name,
                    middle_name: action.middle_name,
                    phone_number: action.phone_number,
                },
            }
        case DELETE_USER_DATA: //  
            return {
                ...state,
                userData: {},
            }
        case SET_AUTH: //  
            return {
                ...state,
                isLogin: action.isLogin,
            }
        case SET_LOGIN_WRONG: //   
            return {
                ...state,
                isLoginWrong: action.isLoginWrong,
            }
        case SET_INITIALIZED: //  
            return {
                ...state,
                isInitialized: true,
            }
        case IS_FETCH: //
            return {
                ...state,
                isFetch: action.isFetch,
            }
        case SET_USER_STATUS: //  
            return {
                ...state,
                isUserServiceAdmin: action.result.is_service_super_admin,
                isUserMuseumSuperAdmin: action.result.is_museum_super_admin,
                isUserMuseumAdmin: action.result.is_museum_admin,
                isUserCashier: action.result.is_museum_cashier,
            }
        case SET_IS_CURRENT_PASSWORD_WRONG: //    
            return {
                ...state,
                isCurrentPasswordWrong: true,
            }
        case SET_IS_PASSWORD_RIGHT: //   
            return {
                ...state,
                isSetPasswordRight: true,
            }
        case SET_IS_PASSWORD_SIMPLE: //  
            return {
                ...state,
                isPasswordSimple: action.isPasswordSimple,
            }
        case SET_IS_EMAIL_EXISTS: //  email
            return {
                ...state,
                isEmailExists: action.isEmailExists,
            }
        case SET_IS_EMAIL_TAKEN: //   email
            return {
                ...state,
                isEmailTaken: action.isEmailTaken,
            }
        case SET_PASSWORD_CONDITIONS: //        
            return {
                ...state,
                isSetPasswordRight: false,
                isCurrentPasswordWrong: false,
            }
        default:
            return state;
    }
}

export const setUserData = (userData) => ({type: SET_USER_DATA, userData}) // 
export const updateUserData = (last_name, first_name, middle_name, phone_number) => ({type: UPDATE_USER_DATA, last_name, first_name, middle_name, phone_number}) //  
export const deleteUserData = () => ({type: DELETE_USER_DATA}) //  
export const setAuth = (isLogin) => ({type: SET_AUTH, isLogin}) //  
export const setLoginWrong = (isLoginWrong) => ({type: SET_LOGIN_WRONG, isLoginWrong}) //  
export const setInitialized = () => ({type: SET_INITIALIZED})
export const setFetch = (isFetch) => ({type: IS_FETCH, isFetch})
export const setUserStatus = (result) => ({type: SET_USER_STATUS, result}) // 
export const setIsPasswordSimple = (isPasswordSimple) => ({type: SET_IS_PASSWORD_SIMPLE, isPasswordSimple})
export const setPasswordConditions = () => ({type: SET_PASSWORD_CONDITIONS}) //        
export const setIsCurrentPasswordWrong = () => ({type: SET_IS_CURRENT_PASSWORD_WRONG}) //  
export const setIsPasswordRight = () => ({type: SET_IS_PASSWORD_RIGHT}) //  
export const setIsEmailExists = (isEmailExists) => ({type: SET_IS_EMAIL_EXISTS, isEmailExists}) //Email   
export const setIsEmailTaken = (isEmailTaken) => ({type: SET_IS_EMAIL_TAKEN, isEmailTaken}) //Email   


export const getStatus = () => { // 
    return (dispatch) => {
        authAPI.getStatus()
            .then(response => response.json()
                .then(result => {
                    console.log('getStatus', result)
                    dispatch(setUserStatus(result))
                    if(result.is_service_super_admin || result.is_museum_super_admin || result.is_museum_admin || result.is_museum_cashier) {
                        dispatch(setAuth(true))
                    }
                    else {
                        dispatch(setAuth(false))
                    }
                    dispatch(setInitialized())
                }))
    }
}

export const accountChange = (last_name, first_name, middle_name, phone_number) => { //  
    return (dispatch) => {
        authAPI.accountChange(last_name, first_name, middle_name, phone_number)
            .then(response => response.json()
                .then(result => {
                    console.log('account Change', result)
                    if(result) {
                        dispatch(updateUserData(last_name, first_name, middle_name, phone_number))
                    }
                }))
    }
}

export const login = (username, password) => { //
    return (dispatch) => {
        authAPI.login(username, password)
            .then(response => response.json()
                .then(result => {
                    if(result.detail !== "No active account found with the given credentials") {
                        console.log('login', result)
                        localStorage.setItem('accessToken', result.access)

                        // dispatch(account()) //    
                        dispatch(getStatus())
                        // dispatch(setAuth(true)) //  
                        dispatch(setLoginWrong(false)) //     
                    }
                    else if(result.detail === "No active account found with the given credentials") {
                        dispatch(setLoginWrong(true)) //       
                    }
                }))
    }
}
export const logout = () => { //
    return (dispatch) => {
        localStorage.removeItem('accessToken');
        dispatch(setAuth(false))
        dispatch(deleteUserData()) //    
    }
}

export const registration = (username, password, email, last_name, first_name, middle_name, phone_number) => { //
    return (dispatch) => {
        dispatch(setFetch(true))
        authAPI.register(username, password, email, last_name, first_name, middle_name, phone_number)
            .then(response => response.json()
                .then(result => {
                    console.log('register', result)
                    if (result.id) {
                        dispatch(setIsEmailTaken(false))
                        dispatch(setIsPasswordSimple(false))
                    }
                    else if(result.username) {
                        dispatch(setIsEmailTaken(true))
                        dispatch(setIsPasswordSimple(false))
                    }
                    else if(result.password) {
                        dispatch(setIsPasswordSimple(true))
                        dispatch(setIsEmailTaken(false))
                    }

                }))
    }
}
export const activation = (uid, token) => { // 
    return (dispatch) => {
        authAPI.activation(uid, token)
            .then(response => response.text()
                .then(result => {
                    console.log('activation', result)
                    dispatch(setFetch(false))
                }))
    }
}

export const setPassword = (current_password, new_password, re_new_password) => { // 
    return (dispatch) => {
        authAPI.setPassword(current_password, new_password, re_new_password)
            .then(response => response.text()
                .then(result => {
                    console.log('set_password', result)
                    if(result === '{"new_password":["    ."]}') {
                        dispatch(setIsPasswordSimple(true))
                    }
                    else if(result === '{"current_password":["Invalid password."]}') { // 
                        dispatch(setIsCurrentPasswordWrong())
                        dispatch(setIsPasswordSimple(false))
                    }
                    else {
                        dispatch(setIsPasswordRight())
                        dispatch(setIsPasswordSimple(false))
                        dispatch(logout())
                    }
                }))
    }
}

export const resetPassword = (email) => { //       
    return (dispatch) => {
        authAPI.accountRecovery(email) // 
            .then(response => response.json()
                .then(result => {
                    console.log('email_recovery')
                    if(result) {
                        dispatch(setIsEmailExists(true))
                        authAPI.resetPassword(email) //   
                            .then(response => response.text()
                                .then(result => {
                                    console.log('reset_password', result)
                                }))
                    }
                    else {
                        dispatch(setIsEmailExists(false))
                    }
                }))

    }
}

export const resetPasswordConfirm = (uid, token, new_password, re_new_password) => { //   
    return (dispatch) => {
        authAPI.resetPasswordConfirm(uid, token, new_password, re_new_password)
            .then(response => response.text()
                .then(result => {
                    console.log('reset_password_confirm', result)
                    if(result === '{"new_password":["    ."]}') {
                        dispatch(setIsPasswordSimple(true))
                    }
                    else {
                        dispatch(setIsPasswordSimple(false))
                        dispatch(logout())
                    }
                }))
    }
}

export const initialized = () => {
    return async (dispatch) => {

    }
}

export default authReducer
