import React from 'react';
import s from './CreateWorkerInputs.module.css'
import Input from "../Input/Input";
import prev from "../../assets/images/left-chevron.svg";

const CreateWorkerInputs = (props) => {
    return (
        <div className={s.container}>
            <button onClick={() => props.history.goBack()} className={'backBtn'}>
                <img src={prev} alt="back"/>
            </button>
            <h1 className={s.title}>  </h1>
            <Input text={''} type={'text'} name={'last_name'} handleFocus={props.handleChangeInputs} handleFindKey={props.handleFindKey} handleChange={props.handleChange} value={props.last_name} />
            <Input text={''} type={'text'} name={'first_name'} handleFocus={props.handleChangeInputs} handleFindKey={props.handleFindKey} handleChange={props.handleChange} value={props.first_name} />
            <Input text={''} type={'text'} name={'middle_name'} handleFocus={props.handleChangeInputs} handleFindKey={props.handleFindKey} handleChange={props.handleChange} value={props.middle_name} />
            <Input text={'Email'} type={'text'} name={'email'} handleFocus={props.handleChangeInputs} handleFindKey={props.handleFindKey} handleChange={props.handleChange} value={props.email} />
            <Input text={''} type={'password'} name={'password'} handleFocus={props.handleChangeInputs} handleFindKey={props.handleFindKey} handleChange={props.handleChange} value={props.password} />
            <Input text={' '} type={'password'} name={'confirm_password'} handleFocus={props.handleChangeInputs} handleFindKey={props.handleFindKey} handleChange={props.handleChange} value={props.confirm_password} />

            {
                !props.isUserServiceAdmin &&
                    <div>
                        <div className={s.role}>
                             
                        </div>
                        <div className={s.InputContainer}><input name={'role'} type="radio" checked={props.role === 'museum_admins'} value='museum_admins' onChange={props.handleChangeRadio}/> </div>
                        <div className={s.InputContainer}><input name={'role'} type="radio" checked={props.role === 'museum_cashiers'} value='museum_cashiers' onChange={props.handleChangeRadio}/></div>
                    </div>
            }

            {
                props.isEmptyInputs &&
                <div className={'form__wrong'}>
                      ,  
                </div>
            }
            {
                !props.isPasswordMatch &&
                <div className={'form__wrong'}>
                      
                </div>
            }
            {
                props.isEmailWrong &&
                <div className={'form__wrong'}>
                    ,   email
                </div>          }

            <button className={'submit'} onClick={props.handleSubmit}></button>

        </div>
    );
}

export default CreateWorkerInputs;
